import Mock from 'mockjs'
import globalData from './data.json'

Mock.mock('/luochen/document/list', 'get', action => {
  let pagination = JSON.parse(action.body)
  // console.log(pagination.search)
  let list = globalData.catalogueList
  // if (pagination.search) {}
  let results = list.slice((pagination.page - 1) * pagination.rows, pagination.page * pagination.rows)
  return {code: 2000, data: {total: list.length, results}, message: '获取成功'}
})

Mock.mock('/luochen/tool/list', 'get', () => {
  return {code: 2000, data: globalData.toolList, message: '获取成功'}
})

Mock.mock('/luochen/blog/list', 'get', (action) => {
  let pagination = JSON.parse(action.body)
  let list = globalData.blogList
  let results = list.slice((pagination.page - 1) * pagination.rows, pagination.page * pagination.rows)
  return {code: 2000, data: {total: list.length, results}, message: '获取成功'}
})


Mock.mock("/luochen/document/new/list", "get", () => {

  let documentList = globalData.catalogueList
  let results = documentList.sort((s1, s2) => {
    return s2.datetime - s1.datetime
  }).slice(0, 7)
  return {code: 2000, data: results, message: '获取成功'}
})

Mock.mock("/luochen/document/hot/list", () => {
  let documentList = globalData.catalogueList
  let results = documentList.sort((s1, s2) => {
    return s2.datetime - s1.datetime
  }).slice(0, 8)
  return {code: 2000, data: results, message: '获取成功'}
})

Mock.mock("/luochen/classify/list", 'get', () => {
  return {code: 2000, data: globalData.classifyList, message: '获取成功'}
})

