import Vue from 'vue'
import Router from 'vue-router'

import IndexView from "../page/IndexView";
import IndexMain from "../page/main/IndexMain";
import ClassifyView from "../page/classify/ClassifyView";
import DocumentViewMain from "../page/document/DocumentViewMain";

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/',
      name: 'IndexView',
      component: IndexView,
      meta: {auth: false},
      children: [
        {
          path: '/',
          name: 'IndexMain',
          component: IndexMain,
          meta: {auth: false},
        },
        {
          path: '/classify/:classifyName',
          name: 'ClassifyView',
          component: ClassifyView,
          meta: {auth: false},
        }
      ]
    },
    {
      path: '/document',
      name: 'DocumentViewMain',
      component: DocumentViewMain,
      meta: {auth: false},
    }
  ],
  // eslint-disable-next-line no-unused-vars
  scrollBehavior(to, from, savedPosition) {
    return {x: 0, y: 0}
  },
})

export default router
