<template>
  <div>
    <div class="card-item-box">
      <div class="card-item-title">{{cardInfo.title}}</div>
      <div class="card-item-content" :style="{ maxHeight: maxHeight + 'px' }">
        <div v-if="cardInfo.list.length > 0">
          <a class="card-item-btn" @click="clickRouterLinkHandle(item)"
             v-for="(item, index) in cardInfo.list" :key="index">
            {{ item.title }}
            <a class="card-item-btn-span"><a-icon type="link"/></a>
          </a>
        </div>
        <empty-list-bar text="暂无数据" v-else/>
      </div>
    </div>
  </div>
</template>

<script>
import EmptyListBar from "./common/EmptyListBar";

export default {
  name: "CardViewBar",
  methods: {
    clickRouterLinkHandle (docuemnt) {
      sessionStorage.setItem("luochen-document-info", JSON.stringify(docuemnt))
      this.$router.push('/document')
    },
  },
  props: {
    maxHeight: { type: Number, default: 200 },
    cardInfo: { type: Object, default: () => { return { title: "", list: [] } } }
  },
  components: { EmptyListBar }
}
</script>

<style scoped>
.card-item-box{width: 100%;height: auto;background: #fff;}
.card-item-box .card-item-title{
  width: 100%;color: #3d3d3d;padding: 0 16px;height: 38px;line-height: 38px;
  font-weight: 700;border-bottom: #f5f6f7 solid 1px;
}
.card-item-content{width: 100%;max-height: 200px;overflow-y: scroll;padding-bottom: .5em}
.card-item-content::-webkit-scrollbar {width: 4px;}
.card-item-content::-webkit-scrollbar-thumb {
  border-radius: 6px;-webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
}
.card-item-content::-webkit-scrollbar-track {border-radius: 0;background: #fff;}

.card-item-content .card-item-btn{
  display: block;
  width: 100%;
  height: 32px;
  padding: 0 16px;
  color: #2d2e2f;
  font-size: 14px;
  line-height: 32px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: .43s;
}
.card-item-content .card-item-btn:hover{color: #fc5531;}

.card-item-content .card-item-btn:hover > .card-item-btn-span{display: inline-block;}
.card-item-content .card-item-btn .card-item-btn-span{display: none;color: #999aaa;transition: .43s;}
.card-item-content .card-item-btn .card-item-btn-span:hover{color: #1890ff;}

</style>
