<template>
  <div>
    <div class="main-content-box">

      <div class="main-content-author-box">
        <author-cart-bar/>
      </div>

      <a-row :gutter="[16,16]">
        <a-col :span="5">

          <div class="main-card-item-box">
            <card-view-bar :max-height="275" :card-info="{title: '热门文章', list: hitDocumentList}"/>
          </div>

          <div class="main-card-item-box">
            <classify-view-bar/>
          </div>

          <div class="main-card-item-box">
            <card-view-bar :max-height="270" :card-info="{title: '最新文章', list: newestDocumentList}"/>
          </div>

        </a-col>
        <a-col :span="19">
          <div class="main-content-list-box">
            <router-view/>
          </div>
        </a-col>
      </a-row>

    </div>
  </div>
</template>

<script>
import AuthorCartBar from "../components/main/AuthorCartBar";
import CardViewBar from "../components/CardViewBar";
import ClassifyViewBar from "../components/ClassifyViewBar";
import apiRequest from "../request/api/ApiRequest";

export default {
  name: "IndexView",
  data () {
    return {
      hitDocumentList: [],
      newestDocumentList: []
    }
  },
  mounted() {
    apiRequest.getDocumentHotList().then(res => {
      if (res.code === 2000) {this.hitDocumentList = res.data}
    })

    apiRequest.getDocumentNewList().then(res => {
      if (res.code === 2000) {this.newestDocumentList = res.data}
    })

  },
  methods: {
    clickClassifyHandle(classify) {
      this.$router.push('/classify/' + classify)
    }
  },
  components: { AuthorCartBar, CardViewBar, ClassifyViewBar }
}
</script>

<style scoped>
.main-content-box{
  width: 100%;
  height: auto;
  padding: 2em;
  background: #EBF4FF;
}
.main-content-box .main-content-author-box{
  margin-bottom: 1em;
}

.main-content-box .main-card-item-box{
  margin-bottom: 1em;
}
.main-content-box .main-content-list-box{
  width: 100%;
  height: auto;
}

.special-column-ul-box{
  width: 100%;margin: 0 auto;padding: 0;list-style: none;max-height: 300px;overflow-y: scroll;
}
.special-column-ul-box::-webkit-scrollbar{width: 0}
.special-column-ul-box .special-column-li-box{
  position: relative;width: 100%;height: 32px;line-height: 32px;padding: 0 32px;text-transform: capitalize;
}
.special-column-ul-box .special-column-li-box:hover{color: #e33e33;}
.special-column-ul-box .special-column-li-box:before{
  position: absolute;content: " ";left: 12px;top: 18px;
  width: 1px;height: calc(100% - 2px);border-left: 1px dashed #ccccd8;
}
.special-column-ul-box .special-column-li-box:last-child:before{content: none;}
.special-column-ul-box .special-column-li-box:after{
  position: absolute;content: " ";top: 15px;left: 12px;width: 16px;
  height: 1px;border-bottom: 1px dashed #ccccd8;
}
.special-column-ul-box .special-column-li-box .special-column-li-tag,
.special-column-ul-box .special-column-li-box .special-column-li-num{
  display: inline-block;font-size: 16px;width: 50%;
}
.special-column-ul-box .special-column-li-box .special-column-li-tag {cursor: pointer;text-align: left;}
.special-column-ul-box .special-column-li-box .special-column-li-num {color: #999aaa;text-align: right;}



.special-column-box {
  width: 100%;
  height: auto;
  padding: 0;
  margin: 0;
  list-style: none;
}
.special-column-box li{
  width: 100%;
  height: auto;
  padding: .3em .8em;
  cursor: pointer;
}
.special-column-box li:hover a, .special-column-box li:hover span{
  color: #fc5531;
}
.special-column-box li a, .special-column-box li span{
  font-size: 14px;
  color: #677788;
  transition: .54s;
}
.special-column-box li span{
  float: right;
}
</style>
