<template>
  <div>
    <div class="classify-item-box">
      <div class="classify-item-title">分类专题</div>
      <ul class="classify-item-content">
        <li class="classify-item-content-item" v-for="(classify, index) in classifyList" :key="index">
          <a @click="clickClassifyhandle(classify)">{{ classify.title }}</a>
          <span>{{ classify.count }}篇</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import apiRequest from "../request/api/ApiRequest";

export default {
  name: "ClassifyViewBar",
  data () {
    return {
      classifyList: []
    }
  },
  mounted() {
    apiRequest.getClassifyList().then(res => {
      if (res.code === 2000) {
        this.classifyList = res.data
      }
    })
  },
  methods: {
    clickClassifyhandle (classify) {
      sessionStorage.setItem('luochen-classify-info', JSON.stringify(classify))
      this.$router.push('/classify/' + classify.title)
    }
  }
}
</script>

<style scoped>
.classify-item-box{width: 100%;height: auto;background: #fff;}
.classify-item-box .classify-item-title{
  width: 100%;color: #3d3d3d;padding: 0 16px;height: 38px;line-height: 38px;
  font-weight: 700;border-bottom: #f5f6f7 solid 1px;
}
.classify-item-box .classify-item-content{
  list-style: none; margin: 0; padding: 0 16px;
  width: 100%;max-height: 200px;overflow-y: scroll;padding-bottom: .5em;
}
.classify-item-box .classify-item-content::-webkit-scrollbar {width: 4px;}
.classify-item-box .classify-item-content::-webkit-scrollbar-thumb {
  border-radius: 6px;-webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
}
.classify-item-box .classify-item-content::-webkit-scrollbar-track {border-radius: 0;background: #fff;}

.classify-item-box .classify-item-content .classify-item-content-item{
  position: relative;
  display: block;
  width: 100%;
  height: 32px;
  padding: 0 16px;
  font-size: 14px;
  line-height: 32px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: .43s;
  /*border-left: #333 dashed 1px;*/
}
.classify-item-box .classify-item-content .classify-item-content-item:before{
  position: absolute;content: ' ';width: 15px;height: 1px;top: 16px;left: 0;
  border-bottom: #ccccd8 dashed 1px;
}
.classify-item-box .classify-item-content .classify-item-content-item:after{
  position: absolute;content: ' ';width: 1px;height: 64px;top: 0;left: 0;
  border-left: #ccccd8 dashed 1px;
}
.classify-item-box .classify-item-content .classify-item-content-item:first-child:after{height: 16px;top: 16px;}
.classify-item-box .classify-item-content .classify-item-content-item:last-child:after{height: 16px;top: 0;}

.classify-item-box .classify-item-content .classify-item-content-item a{
  color: #2d2e2f;
  transition: .43s;
}
.classify-item-box .classify-item-content .classify-item-content-item a:hover{color: #fc5531;}
.classify-item-box .classify-item-content .classify-item-content-item span{
  color: #999aaa;float: right;
}

</style>
