<template>
  <div>
    <!-- 文章列表 -->
    <div class="document-list-body-box">
      <div v-if="documentList.length > 0">
        <div class="document-list-body-item" v-for="(document, index) in documentList" :key="index">
          <div class="document-list-body-item-title">
            <a @click="clickRouterLinkHandle(document)">
              {{ document.title }}
            </a>
          </div>
          <div class="document-list-body-item-footer">
            <span v-for="(tag, index) in document.tags" :key="index">{{ tag }}</span>
            <a-divider type="vertical"/>
            发布时间 {{ timestamp(document.datetime) }}
          </div>
        </div>
      </div>
      <div v-else class="empty-document-box">
        <empty-list-bar text="暂无数据"/>
      </div>
    </div>
    <!-- 文章列表 -->

    <!-- 分页 -->
    <div class="pagination-box">
      <a-pagination :current="documentCurrentPage" :page-size="9" :total="documentTotal"
                    @change="clickPagingationHandle"/>
    </div>
    <!-- 分页 -->
  </div>
</template>

<script>
import EmptyListBar from "../../common/EmptyListBar";
import common from "../../../util/common";

export default {
  name: "MainDocumentListBar",
  methods: {
    clickPagingationHandle (page, rows) {
      this.$emit('clickPagingationHandle', page, rows)
    },
    clickRouterLinkHandle (docuemnt) {
      sessionStorage.setItem("luochen-document-info", JSON.stringify(docuemnt))
      this.$router.push('/document')
    },
    timestamp (timestamp) {
      return common.timestamp(timestamp)
    }
  },
  props: {
    documentCurrentPage: {type: Number, default: 0},
    documentTotal: {type: Number, default: 0},
    documentList: {type: Array, default: () => { return [] }}
  },
  components: { EmptyListBar }
}
</script>

<style scoped>
.document-list-body-box{
  width: 100%;
  height: 755px;
  padding: 1em;
}
.document-list-body-box::-webkit-scrollbar{
  width: 0;
}

.document-list-body-box .empty-document-box{
  padding-top: 5em;
}

.document-list-body-box .document-list-body-item{
  width: 100%;
  height: auto;
  margin-bottom: 1em;
  border-bottom: 1px #f0f0f2 solid;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title{
  margin-bottom: .4em;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title a{
  color: #333333;
  font-size: 18px;
  font-weight: 700;
  transition: .53s;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title a:hover{
  color: #fc5531;
}
.document-list-body-box .document-list-body-item p{
  font-size: 14px;
  margin: .3em 0;
  color: #677788;
}
.document-list-body-box .document-list-body-item .document-list-body-item-footer{
  width: 100%;
  height: auto;
  font-size: 14px;
  margin-bottom: .8em;
}
.document-list-body-box .document-list-body-item .document-list-body-item-footer span{
  display: inline-block;
  padding: .1em .6em;
  font-size: 12px;
  text-transform: capitalize;
  /*color: #e33e33;*/
  /*background: rgba(227,62,51,.1);*/
  color: #1890ff;
  background: #e6f7ff;
  border-radius: 4px;
}

.pagination-box{width: 100%;text-align: right;background: #fff;padding: .8em 1em;
  border-radius: 3px;
}
</style>
