<template>
  <div>
    <div class="main-blog-box">

      <div class="main-blog-item-box" v-for="(blog, index) in blogList" :key="index">
        <a-row>
          <a-col :span="4">
            <div class="main-blog-item-img">
              <img src="https://gong_zheng_ping.gitee.io/images/22.jpg" alt=""/>
            </div>
          </a-col>
          <a-col :span="20">
            <div class="main-blog-item-content">
              <a class="main-blog-item-content-title" :href="blog.link" target="_blank">
                {{ blog.title }}
              </a>
              <div class="main-blog-item-content-text">
                {{ showTextHandle(blog.content) }}
              </div>
            </div>
          </a-col>
        </a-row>
      </div>

    </div>

    <div class="main-blog-pagination-box">
      <a-pagination :current="blogCurrentPage" :page-size="5" :total="blogTotal"
                    @change="clickPagingationHandle"/>
    </div>

  </div>
</template>

<script>

export default {
  name: "MainBlogListBar",
  methods: {
    initMainBlogListView () {
    },
    clickPagingationHandle (page, rows) {
      this.$emit('clickPagingationHandle', page, rows)
    },
    showTextHandle (text) {
      if (text)
        return text.length > 166 ? text.substring(0, 166) + '...' : text
      return text
    }
  },
  props: {
    blogCurrentPage: {type: Number, default: 0},
    blogTotal: {type: Number, default: 0},
    blogList: {type: Array, default: () => { return [] }}
  }
}
</script>

<style scoped>
.main-blog-box{width: 100%;height: 750px;padding: 1em;}

.main-blog-item-box{ width: 100%;height: 115px; margin-bottom: 1.5em; }
.main-blog-item-box .main-blog-item-img, .main-blog-item-box .main-blog-item-img img{
  width: 100%;height: 100%;
}
.main-blog-item-box .main-blog-item-img { height: 115px }
.main-blog-item-box .main-blog-item-content{
  padding-top: .4em;
}
.main-blog-item-box .main-blog-item-content .main-blog-item-content-title{
  width: calc(100% - 1em);line-height: 32px;font-size: 18px;color: #677788;margin-left: 1em;
  cursor: pointer; font-weight: 600; transition: .43s;border-left: #fff solid 4px;
  padding-left: .4em;display: block;
}
.main-blog-item-box .main-blog-item-content:hover .main-blog-item-content-title{
  color: #1890ff; border-color: #1890ff;
}
.main-blog-item-box .main-blog-item-content .main-blog-item-content-text{
  width: 100%;height: auto;padding: 0 1em;font-size: 16px;color: #999aaa;
}
.main-blog-pagination-box{
  text-align: right;
  padding: 1em 0;
  padding-right: 2em;
  border-top: #f0f0f2 solid 1px;
}
</style>
