<template>
  <div>
    <div class="document-list-box">

      <!-- 标题 -->
      <div class="document-list-title-box">
        <a-row>
          <a-col :span="20">
            <a class="document-list-title-item document-list-title-activate" @click="clickTitleHandle($event, 1)">文章</a>
            <a class="document-list-title-item" @click="clickTitleHandle($event, 2)">博客</a>
            <a class="document-list-title-item" @click="clickTitleHandle($event, 3)">工具</a>
          </a-col>
          <a-col :span="4">
            <div class="document-list-title-search-box">
              <input placeholder="搜索查找的文档名字" type="text" @keydown.enter="enterInputHandle"/>
              <span><a-icon type="search"/></span>
            </div>
          </a-col>
        </a-row>
      </div>
      <!-- 标题 -->

      <div>
        <main-document-list-bar v-show="mainDocumentType === 1" :document-current-page="documentPage"
                                :document-total="documentTotal" :document-list="documentList"
                                v-on:clickPagingationHandle="clickPagingationHandle"/>

        <main-blog-list-bar ref="mainBlogListBarRef" v-show="mainDocumentType === 2"
                            :blog-list="blogList" :blog-total="documentTotal"
                            :blog-current-page="documentPage"
                            v-on:clickPagingationHandle="clickPagingationHandle"/>

        <main-tool-list-bar ref="mainToolListBarRef" v-show="mainDocumentType === 3"/>
      </div>

    </div>

  </div>
</template>

<script>
import MainDocumentListBar from "../../components/main/list/MainDocumentListBar";
import MainBlogListBar from "../../components/main/list/MainBlogListBar";
import MainToolListBar from "../../components/main/list/MainToolListBar";
import apiRequest from "../../request/api/ApiRequest";

export default {
  name: "IndexMain",
  data () {
    return {
      mainDocumentType: 1,
      searchInputText: '',
      documentList: [],
      documentTotal: 0,
      documentPage: 1,
      blogList: []
    }
  },
  mounted() {
    this.loadDocumentListHandle({page: this.documentPage, rows: 9})
  },
  methods: {
    loadDocumentListHandle (pagination) {
      apiRequest.getDocumentList(pagination).then(res => {
        if (res.code === 2000) {
          this.documentList = res.data.results
          this.documentTotal = res.data.total
        }
      })
    },
    loadBlogListHandle (pagination) {
      apiRequest.getBlogList(pagination).then(res => {
        if (res.code === 2000) {
          this.blogList = res.data.results
          this.documentTotal = res.data.total
        }
      })
    },
    clickPagingationHandle (page, rows) {
      this.documentPage = page
      if (this.mainDocumentType === 1) this.loadDocumentListHandle({page, rows})
      else if (this.mainDocumentType === 2) this.loadBlogListHandle({page, rows})
    },
    enterInputHandle (e) {
      this.loadDocumentListHandle ({page: this.documentPage, rows: 9, search: e.target.value})
    },
    clickTitleHandle (e, type) {
      this.documentPage = 1
      let list = document.getElementsByClassName('document-list-title-item')
      for (const elem of list)
        elem.classList.remove('document-list-title-activate')
      e.target.classList.add('document-list-title-activate')
      this.mainDocumentType = type
      switch (type) {
        case 1:
          this.loadDocumentListHandle({ page: this.documentPage, rows: 9 })
          break
        case 2:
          this.loadBlogListHandle({page: 1, rows: 5})
          break
        case 3:
          this.$refs.mainToolListBarRef.initMainToolListView()
          break
      }
    }
  },
  components: { MainDocumentListBar, MainBlogListBar, MainToolListBar }
}
</script>

<style scoped>
.document-list-box{
  width: 100%;
  height: auto;
  min-height: 766px;
  background: #fff;
  border-radius: 2px;
}
.document-list-box .document-list-title-box{
  width: 100%;
  height: 42px;
  padding: 0 1em;
  border-bottom: 1px #f0f0f2 solid;
}
.document-list-box .document-list-title-box .document-list-title-item{
  display: inline-block;
  color: #677788;
  height: 41px;
  padding: 0 .3em;
  line-height: 42px;
  margin-right: 2em;
  transition: .54s;
  border-bottom: #fff solid 2px;
}
.document-list-box .document-list-title-box .document-list-title-activate{
  color: #333333;
  border-bottom-color: #333333;
}
.document-list-box .document-list-title-box .document-list-title-item:hover {
  color: #333333;
}
.document-list-box .document-list-title-box .document-list-title-search-box{
  width: 100%;
  height: 28px;
  margin: 7px 0;
  padding: 1px;
  background: #f0f0f2;
  border-radius: 30px;
}
.document-list-box .document-list-title-box .document-list-title-search-box input{
  width: 88%;
  height: 26px;
  outline: none;
  border: none;
  padding: 0 1em;
  font-size: 14px;
  color: #677788;
  background: #f0f0f2;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.document-list-box .document-list-title-box .document-list-title-search-box span{
  display: inline-block;
  font-size: 14px;
  width: 12%;
  height: 28px;
  color: #677788;
  cursor: pointer;
  line-height: 28px;
}

</style>
