
const common = {

  timestamp (timestamp) {
    let datetime = new Date(timestamp)
    return datetime.getFullYear() + '-' + this.addZero((datetime.getMonth() + 1)) + '-' +
        this.addZero(datetime.getDate()) + ' ' + this.addZero(datetime.getHours()) + ':' +
        this.addZero(datetime.getSeconds())
  },

  timestampShow (timestamp) {
    let datetime = new Date(timestamp)
    return datetime.getFullYear() + '-' + this.addZero((datetime.getMonth() + 1)) + '-' + this.addZero(datetime.getDate())
  },

  simpleTimestamp (timestamp) {
    let datetime = new Date(timestamp)
    return this.addZero((datetime.getMonth() + 1)) + '/' + this.addZero(datetime.getDate())
  },

  addZero (num) {
    return num > 9 ? num : '0' + num
  },

  guid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      let r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  },

  encryption (value) {
    let monyer = ""
    for(let i = 0; i < value.length; i++)
      monyer += "@" + value.charCodeAt(i).toString(8);
    return monyer
  },

  deciphering (value) {
    let monyer = ''
    let s = value.split("@");
    for(let i = 1; i < s.length; i++)
      monyer += String.fromCharCode(parseInt(s[i],8));
    return monyer
  },

  catalogueListHandle (menuList, hTags) {
    let catalogueList = [], previousIndent = 0, index = 1, pid = 0;
    for (const el of menuList) {
      let indent = hTags.indexOf(el.tagName)
      if (indent > previousIndent) { pid = index - 1 }
      else if (indent !== previousIndent) { pid = 0 }

      catalogueList.push({
        id: index,
        title: el.innerText,
        targetId: el.getAttribute('id'),
        indent: indent,
        pid: pid
      })
      index += 1
      previousIndent = indent
    }
    return catalogueList;
  },

  completeSubmenus(menu, menuList) {
    const submenus = []
    for (const item of menuList) {
      if (item.pid === menu.id) {submenus.push(item)}
    }
    if (submenus.length > 0) {
      for (const submenu of submenus) {this.completeSubmenus(submenu, menuList)}
      menu.submenus = submenus
    }
    return menu
  }

}

export default common
