<template>
  <div>
    <div class="document-main-box">
      <div class="document-main-x-box">
        <a-row :gutter="[16,16]">
          <a-col :span="6">
            <div class="document-main-left-box">

              <!-- 作者信息 -->
              <div class="document-main-left-item">
                <author-card-view-bar/>
              </div>
              <!-- 作者信息 -->

              <!-- 最新文章 -->
              <div class="document-main-left-item">
                <card-view-bar :card-info="newDocumentList"/>
              </div>
              <!-- 最新文章 -->

              <div class="document-main-left-item">
                <classify-view-bar/>
              </div>

              <!-- 热门文章 -->
              <div class="document-main-left-item">
                <card-view-bar :card-info="hitDocumentList"/>
              </div>
              <!-- 热门文章 -->

              <!-- 目录 -->
              <div class="document-main-left-item" ref="catalogueItemRef">
                <catalogue-item-bar :current-activate-catalogue="currentActivateCatalogue"
                                    :catalogue-list="catalogueList"/>
              </div>
              <!-- 目录 -->

            </div>
          </a-col>
          <a-col :span="18">
            <div class="document-main-right-box">
              <div class="document-main-right-content">
                <component v-bind:is="document" key="document" ref="documentMainContentBox"
                           v-highlight></component>
              </div>
            </div>
          </a-col>
        </a-row>
      </div>
    </div>

    <a-back-top>
      <div class="back-top-box"><a-icon type="vertical-align-top"/></div>
    </a-back-top>

    <a-drawer :title="null" :width="'100%'" placement="right" :closable="false"
              :visible="bigImgVisible" @close="handleCancel">
      <div class="image-viewer__wrapper">
        <div class="image-viewer__canvas" @click="bigImgVisible = false">
          <img :src="currentImgPath" alt=""/>
        </div>
      </div>
    </a-drawer>

  </div>
</template>

<script>
import AuthorCardViewBar from "../../components/AuthorCardViewBar";
import CardViewBar from "../../components/CardViewBar";
import CatalogueItemBar from "../../components/document/CatalogueItemBar";
import ClassifyViewBar from "../../components/ClassifyViewBar";
import common from "../../util/common";
import apiRequest from "../../request/api/ApiRequest";

export default {
  name: "DocumentViewMain",
  data(){
    return {
      document: undefined,
      catalogueList: [],
      currentActivateCatalogue: '',
      catalogueScrollTop: 0,
      catalogueListScrollTop: [],
      bigImgVisible: false,
      currentImgPath: '',
      newDocumentList: {
        title: "最新文章",
        list: [
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
        ]
      },
      hitDocumentList: {
        title: "热门文章",
        list: [
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
          {title: "Spring Boot-整合定时器", "link": ""}, {title: "Redis的使用", "link": ""},
        ]
      }
    }
  },
  mounted () {
    let documentInfo = sessionStorage.getItem('luochen-document-info')
    if (documentInfo) {
      this.documentInfo = JSON.parse(documentInfo)
      if (this.documentInfo.path)
        this.document = () => import('../../assets/documents' + this.documentInfo.path)
    } else {
      this.$router.push(this.upperRoutePath)
    }
    setTimeout(() => {
      let ele = this.$refs.documentMainContentBox
      if (ele) this.loadCatalogueList(ele)
    }, 1000)

    apiRequest.getDocumentNewList().then(res => {
      if (res.code === 2000) {this.newDocumentList.list = res.data}
    })
    apiRequest.getDocumentHotList().then(res => {
      if (res.code === 2000) {this.hitDocumentList.list = res.data}
    })

    this.getCatalogueScrollTopHandle()
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleCancel () {
      this.bigImgVisible = false
    },
    loadCatalogueList(ele) {
      let titles = { value: '' }
      let anchors = ele.$el.querySelectorAll("h1,h2,h3,h4,h5,h6")
      titles.value = Array.from(anchors).filter((title) => !!title.innerText.trim())
      const hTags = Array.from(new Set(titles.value.map((title) => title.tagName))).sort()
      titles.value.map((el => {el.setAttribute('id', common.guid())}))

      this.catalogueList = common.catalogueListHandle(titles.value, hTags)
      for (const item of this.catalogueList) {
        this.catalogueListScrollTop.push({
          targetId: item.targetId,
          scrollTop: document.getElementById(item.targetId).offsetTop
        })
      }

      let imgList = ele.$el.querySelectorAll("img")
      const that = this
      imgList.forEach(img => {
        img.onclick = (e) => {
          that.bigImgVisible = true;
          this.currentImgPath = e.target.currentSrc;
          e.stopPropagation()
        }
      })
    },
    documentTitleScrollHandle(scrollTop) {
      for (const item of this.catalogueListScrollTop){
        if (scrollTop >= item.scrollTop) {
          this.currentActivateCatalogue = item.targetId
        }
      }
    },
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      this.documentTitleScrollHandle(scrollTop)
      let w = 0
      if (this.$refs.catalogueItemRef) {w = this.$refs.catalogueItemRef.offsetWidth}
      if (scrollTop >= this.catalogueScrollTop) {
        this.$refs.catalogueItemRef.style.position = 'fixed'
        this.$refs.catalogueItemRef.style.top = '0'
        this.$refs.catalogueItemRef.style.width = w + 'px'
      } else {
        this.$refs.catalogueItemRef.style.position = ''
        this.$refs.catalogueItemRef.style.top = '0'
        this.$refs.catalogueItemRef.style.width = '100%'
      }
    },
    getCatalogueScrollTopHandle() {
      if (this.$refs.catalogueItemRef) {
        this.catalogueScrollTop = this.$refs.catalogueItemRef.getBoundingClientRect().top
      }
    }
  },
  components: { AuthorCardViewBar, CardViewBar, CatalogueItemBar, ClassifyViewBar }
}
</script>

<style scoped>
.document-main-box{width: 100%;height: auto;background: #f8f8f8;padding: 2em 0;}
.document-main-x-box{width: 90%;height: auto;margin: 0 auto;}
.document-main-left-box, .document-main-right-box{width: 100%;height: auto;}

.document-main-left-box .document-main-left-item{width: 100%;height: auto;margin-bottom: 1em;overflow: hidden;}
.document-main-left-box .document-main-left-item:last-child{margin-bottom: 0;}

.back-top-box{position: fixed;bottom: 1em;right: 1em;width: 40px;height: 40px;
  line-height: 42px;overflow: hidden;color: rgba(0,0,0,.45);
  background: rgba(255, 255, 255, .8);text-align: center;border-radius: 4px;
  font-size: 20px;cursor: pointer;transition: all .3s cubic-bezier(.645,.045,.355,1);
}
.back-top-box:hover{ background: #fff;color: #000; }

.document-main-right-content{
  width: 100%;height: auto;background: #fff; padding: 2em;min-height: 100vh;
}

/* 大图 */
.image-viewer__wrapper{position: fixed;width:100%;height:100%;top: 0;right: 0;bottom: 0;left: 0;}
.image-viewer__wrapper .image-viewer__canvas{
  width: 100%;height: 100%;display: flex;justify-content: center;align-items: center;
}
.image-viewer__wrapper .image-viewer__canvas img{
  overflow-clip-margin: content-box;transform: scale(1) rotate(0deg);margin-left: 0;
  margin-top: 0;max-height: 100%;max-width: 100%;cursor: zoom-out;
}
/* 大图 */

/* 字体大小 */
.document-main-right-content >>> *{font-size: 16px;}
/* 字体大小 */

/* 图片 */
.document-main-right-content >>> img{display: block;margin: 0 auto;width: 100%;height: 100%;cursor: zoom-in}
/* 图片 */

/* 代码块 */
.document-main-right-content >>> pre{
  position: relative;background: #2b2b2b;font-size: 14px;border-radius: 4px;
  padding: 30px 0 0 0;tab-size:2;
}
.document-main-right-content >>> pre code{
  display: block;width: 100%;height: auto;border-top: #ccc solid 1px;
}
.document-main-right-content >>> pre code::-webkit-scrollbar{height: 6px;}
.document-main-right-content >>> pre code::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
  background: #c1c9c9;
}
.document-main-right-content >>> pre code::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(87, 175, 187, 0.1);border-radius: 10px;background: #2b2b2b;
}
.document-main-right-content >>> pre::before{
  content: '';position: absolute;top: 9px;left: 10px;width: 12px;height: 12px;
  border-radius: 50%;background: #fc625d;box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;z-index: 1;
}
/* 代码块 */

/* 标题 */
.document-main-right-content >>> h1, .document-main-right-content >>> h2, .document-main-right-content >>> h3,
.document-main-right-content >>> h4, .document-main-right-content >>> h5, .document-main-right-content >>> h6{
  margin-top: 2em;
  margin-bottom: .8em;
  font-weight: 600;
  line-height: 1.2;
  color: #1e2022;
}
.document-main-right-content >>> h1{font-size: 22.75px;}
.document-main-right-content >>> h2{font-size: 19.50px;}
.document-main-right-content >>> h3{font-size: 17.55px;}
.document-main-right-content >>> h4{font-size: 16.25px;}
.document-main-right-content >>> h5{font-size: 14.95px;}
.document-main-right-content >>> h6{font-size: 13.95px;}
/* 标题 */

/* 大标题 */
.document-main-right-content >>> h1:first-child{
  text-align: center;margin-top: 0;padding-bottom: .5em;border-bottom: #f9fafc solid 1px;
}
/* 大标题 */

/* 表格 */
.document-main-right-content >>> table {width: 100%; border: #e7eaf3 solid 1px;}
.document-main-right-content >>> table tr th, .document-main-right-content >>> table tr td{
  padding: .6em 1em;border: #e7eaf3 solid 1px;
}
/* 表格 */

/* 块引用 */
.document-main-right-content >>> blockquote{
  line-height: 26px;margin-bottom: 18px;background: #ecf8ff;border-left: 5px solid #50bfff;
  color: #606266;padding: 8px 15px;border-radius: 0 4px 4px 0;
}
/* 块引用 */

</style>
