<template>
  <div>
    <div class="main-tools-box">
      <a-row :gutter="[16,16]">

        <a-col :span="4" v-for="(tool, index) in toolList" :key="index">
          <div class="main-tools-item">
            <div class="main-tools-item-img">
              <img :src="tool.img" alt=""/>
            </div>
            <a class="main-tools-item-text" target="_blank" :href="tool.link">{{ tool.name }}</a>
          </div>
        </a-col>

      </a-row>
    </div>
  </div>
</template>

<script>
import apiRequest from "../../../request/api/ApiRequest";

export default {
  name: "MainToolListBar",
  data () {
    return {
      toolList: []
    }
  },
  methods: {
    initMainToolListView () {
      apiRequest.getToolList().then(res => {
        if (res.code === 2000) this.toolList = res.data
      })
    },
    showImgHandle (imgPath) {
      console.log("'" + imgPath + "'")
      // return require('../../../assets/tool-img/youdao-img.png')
      return imgPath
    }
  }
}
</script>

<style scoped>
.main-tools-box{width: 100%;height: 815px;padding: 1em;overflow-y: scroll;}
.main-tools-box::-webkit-scrollbar{width: 0}
.main-tools-item{
  width: 100%;height: auto;background: #fff;border-radius: 0.625rem;
  border: #fff solid 1px;transition: .43s;text-align: center;padding: 1em 0;
}
.main-tools-item:hover{border-color: #F9F9F9;box-shadow: 0px 10px 30px 0px rgba(82, 63, 105, 0.1);}

.main-tools-item .main-tools-item-img{
  width: 100px;height: 100px; margin: 0 auto;padding: .4em;border: #fff solid 1px;
  border-radius: 12px;transition: .43s;
}
.main-tools-item:hover .main-tools-item-img{border-color: #1890ff;}

.main-tools-item .main-tools-item-img img{width: 100%; height: 100%;}
.main-tools-item .main-tools-item-text{
  display: block;margin-top: .5em;font-size: 14px;font-weight: 600;color: #677788;
  cursor: pointer; transition: .43s;
}
.main-tools-item .main-tools-item-text:hover{color: #1890ff;}

</style>
