<template>
  <div>
    <div class="catalogue-item-box">
      <div class="catalogue-item-title">目录</div>
      <div class="catalogue-item-content">
        <a class="catalogue-item-btn" v-for="(item, index) in catalogueList"
           :key="index" :class="item.targetId + ' h-' + item.indent" @click="catalogueTitleHandle($event, item)">
          {{ item.title }}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CatalogueItemBar",
  watch: {
    currentActivateCatalogue: {
      handler(value){
        if (value && value.length) {
          let element = document.getElementsByClassName(value)[0]
          this.catalogueTitleActivateHandle(element)
        }
      },
      deep: true
    }
  },
  methods: {
    catalogueTitleHandle(event, catalogue) {
      document.getElementById(catalogue.targetId).scrollIntoView({ block: "start", behavior: "smooth" })
      this.catalogueTitleActivateHandle(event.target)
    },
    catalogueTitleActivateHandle(element) {
      let itemList = document.getElementsByClassName('catalogue-item-btn')
      for (const item of itemList) {item.classList.remove('catalogue-item-btn-activate')}
      element.classList.add('catalogue-item-btn-activate')
      // document.querySelector('.catalogue-item-btn-activate').scrollIntoView()
    }
  },
  props: {
    currentActivateCatalogue: {type: String, default: ''},
    catalogueList: {type: Array, default: () => { return [] }}
  }
}
</script>

<style scoped>
.catalogue-item-box{width: 100%;height: auto;background: #fff;overflow: hidden;}
.catalogue-item-box .catalogue-item-title{
  width: 100%;color: #3d3d3d;padding: 0 16px;height: 38px;line-height: 38px;
  font-weight: 700;border-bottom: #f5f6f7 solid 1px;
}
.catalogue-item-box .catalogue-item-content{
  list-style: none;padding: 0;margin: 0;width: 100%;overflow-y: scroll;
  max-height: calc(100vh - 38px - 40px);padding-bottom: .5em;
}
.catalogue-item-box .catalogue-item-content::-webkit-scrollbar {width: 4px;}
.catalogue-item-box .catalogue-item-content::-webkit-scrollbar-thumb {
  border-radius: 6px;-webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
}
.catalogue-item-box .catalogue-item-content::-webkit-scrollbar-track {
  border-radius: 0;background: #fff;
}

.catalogue-item-box .catalogue-item-content .catalogue-item-btn{
  display: block;
  width: 100%;
  height: 32px;
  padding: 0 16px;
  color: #2d2e2f;
  font-size: 14px;
  line-height: 32px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: .43s;
}
.catalogue-item-box .catalogue-item-content .catalogue-item-btn:hover{background: #f0f0f5;}
.catalogue-item-box .catalogue-item-content .catalogue-item-btn-activate{color: #e33e33;background: #f0f0f5;}


.catalogue-item-box .catalogue-item-content .h-1{padding-left: 2em;}
.catalogue-item-box .catalogue-item-content .h-2{padding-left: 3em;}
.catalogue-item-box .catalogue-item-content .h-3{padding-left: 4em;}
.catalogue-item-box .catalogue-item-content .h-4{padding-left: 5em;}
.catalogue-item-box .catalogue-item-content .h-5{padding-left: 6em;}
.catalogue-item-box .catalogue-item-content .h-6{padding-left: 7em;}

.catalogue-item-box .catalogue-item-content >>> .ant-anchor-ink{
  display: none !important;
}

</style>
