<template>
  <div>
    <div class="classify-header-box">
      <a-row>
        <a-col :span="4">
          <div class="classify-header-img">
            <img :src="classifyInfo.img" alt=""/>
          </div>
        </a-col>
        <a-col :span="20">
          <div class="classify-header-body">
            <div class="classify-header-body-title">{{ classifyInfo.title }}</div>
            <div class="classify-header-body-desc">{{ classifyInfo.desc }}</div>
            <div class="classify-header-body-footer">
              <span>文章数：{{ classifyInfo.count }}</span>
            </div>
          </div>
        </a-col>
      </a-row>
    </div>

    <div class="classify-body-box">
      <main-document-list-bar :document-current-page="documentCurrentPage"
                              :document-total="documentTotal" :document-list="documentList"
                              v-on:clickPagingationHandle="clickPagingationHandle"/>
    </div>

  </div>
</template>

<script>
import MainDocumentListBar from "../../components/main/list/MainDocumentListBar";
import apiRequest from "../../request/api/ApiRequest";

export default {
  name: "ClassifyView",
  data () {
    return {
      classifyInfo: {},
      documentCurrentPage: 1,
      documentTotal: 0,
      documentList: []
    }
  },
  mounted() {
    let classifyInfo = sessionStorage.getItem('luochen-classify-info')
    if (classifyInfo) {
      this.classifyInfo = JSON.parse(classifyInfo)
    } else {
      this.$router.push('/')
    }
    this.getPaginationDataHandle({page: this.documentCurrentPage, rows: 9})
  },
  methods: {
    clickPagingationHandle(page) {
      this.documentCurrentPage = page
      this.getPaginationDataHandle({page, rows: 9})
    },
    getPaginationDataHandle(pagination) {
      apiRequest.getDocumentList(pagination).then(res => {
        if (res.code === 2000) {
          this.documentList = res.data.results
          this.documentTotal = res.data.total
        }
      })
    }
  },
  components: { MainDocumentListBar }
}
</script>

<style scoped>
.classify-header-box{
  width: 100%;height: 100%;background: #fff;
}
.classify-header-box .classify-header-img{
  width: 200px;
  height: 200px;
  padding: 2rem;
}
.classify-header-box .classify-header-img img{ width: 100%;height: 100%;border-radius: 6px; }
.classify-header-body {padding-top: 2rem;padding-right: 2rem;}
.classify-header-body .classify-header-body-title{font-weight: 600;font-size: 20px;}
.classify-header-body .classify-header-body-desc{
  text-indent: 2em;font-size: 16px;font-weight: 400;max-height: 72px;
}
.classify-header-body .classify-header-body-footer{
  margin-top: .8em;font-weight: 400;font-size: 16px;
}

.classify-body-box{
  width: 100%;
  height: auto;
  margin-top: 1em;
  background: #fff;
}

</style>
