import http from "../http";

const apiRequest = {

  getDocumentList (pagination) {
    return http.get('/luochen/document/list', pagination)
  },

  getDocumentNewList () {
    return http.get("/luochen/document/new/list", {})
  },

  getDocumentHotList () {
    return http.get("/luochen/document/hot/list", {})
  },

  getBlogList (pagination) {
    return http.get('/luochen/blog/list', pagination)
  },

  getToolList () {
    return http.get('/luochen/tool/list', {})
  },

  getClassifyList() {
    return http.get('/luochen/classify/list', {})
  }

}

export default apiRequest
